//Footer

.footer {
    margin-top:20px;
    height: 430px;
    position: relative;
    &__info {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
        height: 90px;
        background-color: #f5ed93bb;
        padding-top: 10px;

        display: none;
    }
    &__addr {
        margin-bottom: 6px;
        color: #160e0e;
        font-size: 20px;
        line-height: 20px;
        font-weight: 500;
        text-align: center;
    }
    &__social-item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 50px;
        height: 50px;
        // border: 2px solid #160e0e;
        border-radius: 100%;
        margin: 0 15px;
        img {
            height: 50px;
        }
    }
    &__social {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    iframe {
        width: 100%;
        height: 430px;
        border: none;
    }
}

//tabs
.info {
    width: 100%;
    min-height: 580px;
    background-color: $dark;
    padding: 50px;
    box-shadow: 0px 0px 30px rgba(0,0,0, .8);
    &-header {
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: space-around;
        &-tab {
            color: $main-color;
            font-weight: bold;
            font-size: 24px;
            cursor: pointer;
            &:after {
                content: '';
                display: block;
                width: 0%;
                height: 3px;
                background-color: $main-color;
                margin-top: 4px;
                transition:0.4s;
            }
            &:hover {
                &:after {
                    width: 60%;
                }
            }
        }
    }
    &-tabcontent {
        width: 100%;
        display: flex;
        padding-top: 50px;
        transition: 0.3s;
        .description{
            width: 50%;
            &-title {
                font-size: 34px;
                color: $main-color;
                &:after {
                    content: '';
                    display: block;
                    width: 20%;
                    height: 3px;
                    background-color: $main-color;
                    margin-top: 4px;
                }
            }
            &-text {
                margin-top: 30px;
                color: #fff;
                font-weight: 300;
                padding-right: 20px;
            }
            &-btn {
                width: 180px;
                height:45px;
                line-height: 43px;
                margin-top: 30px;
                border: 1px solid $main-color;
                font-size: 14px;
                color: $main-color;
                text-transform: uppercase;
                font-weight: bold;
                text-align: center;
                font-weight: 300;
                cursor: pointer;
                &:hover {
                    box-shadow: 0px 0px 5px $main-color;
                }
            }
        }
        .photo {
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            .img{
                height: 100%;
                width: auto;
                box-shadow: 0px 0px 30px rgba(0,0,0, .8);
            }
        }
    }
    .hide {
        display:none;
    }
    .show {
        display: flex;
    }
}

.img{
    max-width: 250px;
    height: 300px;
    img{
        overflow: hidden;
    }
}

